.sessions {
  margin: 0 auto 28px;
}

.sessions-list-item-svg {
  width: 24px;
  height: 24px;
}

.cont {
  max-width: 320px;
  padding: 0 16px;
}

.sessions-list-item {
  border: 1px solid #b4f5ff;
  border-radius: 16px;
  align-items: center;
  gap: 20px;
  display: flex;
}

.session_link {
  align-items: center;
  gap: 20px;
  padding: 12px 4px;
  display: flex;
}

.sessions-list-item-text {
  font-size: 12px;
  font-weight: 400;
  line-height: 1.4;
}

.sessions-list-item-title {
  background-color: #f4f5f9;
  border: none;
  gap: 13px;
  padding: 12px 4px;
}

.unchecked {
  border: 1px solid #fec204;
}

.sessions-list-item + .sessions-list-item {
  margin-top: 4px;
}

